<template>
    <div class="hello">
        <div class="home__container">
            <div class="home__title">
                Address Profile: {{addressProfile}}
            </div>
            <div class="home__title">
                Address Wallet: {{addressWallet}}
            </div>
            <div class="home__title">
                Full Name: {{fullName}}
            </div>
            <div class="home__title">
                KYC: {{KYC}}
            </div>
            <br> 
            <div class="home__title">
                Confirmations:
            </div>
            <div class="home__cards">
                <div class="card" v-for="obj, index in confirmations" :key="index" style="width: 18rem;">
                    <div class="card-body">
                        <h5 class="card-title">Metric Name: {{obj.name}}</h5>
                        <p @click="handleToOracle(obj.oracle)" class="cursor-pointer card-text">Oracle address: {{obj.oracle}}</p>
                        <p class="card-text">Metric label: {{obj.label}}</p>
                        <p class="card-text">Oracle label: {{obj.oracleLabel}}</p>
                    </div>
                </div>
            </div>
            <div class="home__title">
                Metric Names: 
            </div>
            <div class="home__cards">
                <div class="card" v-for="obj, index in nameMetrics" :key="index" style="width: 18rem;">
                    <div class="card-body">
                        <h5 class="card-title">Metric Name: {{obj.name}}</h5>
                        <p @click="handleToOracle(obj.oracle)" class="cursor-pointer card-text">Oracle address: {{obj.oracle}}</p>
                        <p class="card-text">Metric label: {{obj.label}}</p>
                        <p class="card-text">Oracle label: {{obj.oracleLabel}}</p>
                    </div>
                </div>
            </div>
            <br>
            <br>
            <div class="home__title">
                Metrics:
            </div>
            <div class="home__cards">
                <div class="card" v-for="obj, index in metrics" :key="index" style="width: 18rem;">
                    <div class="card-body">
                        <h5 class="card-title">{{obj.label}}</h5>
                        <p class="card-text">{{obj.result}}</p>
                    </div>
                </div>
            </div>
            
        </div>
        <button @click="handleMain" type="button" class="back btn btn-warning">MAIN</button>

    </div>
</template>

<script>
import router from '@/router'
import {
  fetchContractData
} from '@/utils/web3.js';
import { 
    abiDataRouter,
  abiMaster,
  abiProfileContract,
} from '@/utils/abi.js'
import { 
  ADDR_DATA_ROUTER,
  ADDR_MASTER
} from '@/utils/addresses.js'
import { 
  methodsByType,
  converter,
  getHash
} from '@/utils/metricTypes.js'


export default {    
    name: 'ProfileAddress',
    async created() {
        this.addressProfile = this.$route.params.address
        this.addressWallet = await fetchContractData(abiProfileContract, this.addressProfile, 'addressWallet', [])
        this.fullName = await fetchContractData(abiProfileContract, this.addressProfile, 'fullName', [])
        this.KYC = await fetchContractData(abiProfileContract, this.addressProfile, 'KYC', [])
        const lengthConfirmations = await fetchContractData(abiProfileContract, this.addressProfile, 'getLengthConfirmations', [])
        const lengthMetricNames = await fetchContractData(abiProfileContract, this.addressProfile, 'getLengthMetricNames', [])
        if(Number(lengthConfirmations)) {
            let confirmations = await fetchContractData(abiProfileContract, this.addressProfile, 'getSliceConfirmations', [0, lengthConfirmations])
            for(let i = 0; i < confirmations.length; i++) {
                const hash = getHash(confirmations[i]);
                const metricinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getMetricaInfo', [hash])
                const oracleinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getOracleInfo', [metricinfo.oracle])
                let info = {
                    name: metricinfo.name,
                    oracle: metricinfo.oracle,
                    label: metricinfo.label,
                    oracleLabel: oracleinfo['1']
                }
                this.confirmations.push(info)
            }
        }
        if(Number(lengthMetricNames)) {
            const nameMetrics = await fetchContractData(abiProfileContract, this.addressProfile, 'getSliceMetricNames', [0, lengthMetricNames])
            for(let i = 0; i < nameMetrics.length; i++) {
                const hash = getHash(nameMetrics[i]);
                const metricinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getMetricaInfo', [hash])
                const oracleinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getOracleInfo', [metricinfo.oracle])
                let info = {
                    name: metricinfo.name,
                    oracle: metricinfo.oracle,
                    label: metricinfo.label,
                    oracleLabel: oracleinfo['1']
                }
                this.nameMetrics.push(info)
                const metricType = await fetchContractData(abiDataRouter, ADDR_DATA_ROUTER, 'getTypeMetricByName', [nameMetrics[i]])
                const getMethod = methodsByType[metricType];
                const result = await fetchContractData(abiDataRouter, ADDR_DATA_ROUTER, getMethod, [this.addressProfile, nameMetrics[i]])
                const converted = converter(result, metricType);
                this.metrics.push({
                    result: converted,
                    label: metricinfo.label
                });
            }
                
        }
    },
    data() {
        return {
            addressProfile: '',
            fullName: '',
            addressWallet: '',
            KYC: '',
            confirmations: [],
            nameMetrics: [],
            metrics: []
        }
    },
    methods: {
        handleMain() {
            router.push('/');
        },
        handleToOracle(address) {
            router.push(`/oracle-address/${address}`)
        },
    }
}
</script>

<style scoped>
.back {
    position: absolute;
    left: 40px;
    top: 60px;
}
.cursor-pointer {
    cursor: pointer
}
.home__container {
    width: 900px;
    margin: 0 auto;   
}
.home__title {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 20px;
}
.home__cards {
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    flex-wrap: wrap;
}
</style>