<template>
    <div class="hello">
        <div class="home__container">
            <div class="home__title">
                Address Oracle: {{addressOracle}}
            </div>
            <div class="home__title">
                Oracle Label: {{oracleLabel}}
            </div>
            <div class="home__title">
                Karma: {{karma}}
            </div>
            <div class="home__title">
                BAN: {{isBan}}
            </div>
            <div class="home__title">
                All Metrics: 
            </div>
            <div class="home__cards">
                <div class="card" v-for="obj, index in metrics" :key="index" style="width: 18rem;">
                    <div class="card-body">
                        <h5 class="card-title">Metric Name: {{obj.name}}</h5>
                        <p class="card-text">Metric label: {{obj.label}}</p>
                        <p class="card-text">Metric type: {{obj.type}}</p>
                    </div>
                </div>
            </div>
        </div>
        <button @click="handleMain" type="button" class="back btn btn-warning">MAIN</button>
    </div>
</template>

<script>
import router from '@/router'
import {
  fetchContractData
} from '@/utils/web3.js';
import { 
  abiMaster,
} from '@/utils/abi.js'
import { 
  ADDR_MASTER
} from '@/utils/addresses.js'
import { 
  getHash,
typeStringByType
} from '@/utils/metricTypes.js'

export default {
    name: 'OracleAddress',
    async created() {
        this.addressOracle = this.$route.params.address
        const oracleinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getOracleInfo', [this.addressOracle]) 
        const allMetrics = oracleinfo[0] 
        this.oracleLabel = oracleinfo[1]
        this.karma = oracleinfo[2]
        this.isBan = oracleinfo[3]
        if(allMetrics.length) {
            for(let i = 0; i < allMetrics.length; i++) {
                const hash = getHash(allMetrics[i]);
                const metricinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getMetricaInfo', [hash])
                const typeInString = typeStringByType[metricinfo[1]]
                let info = {
                    name: metricinfo.name,
                    label: metricinfo.label,
                    type: typeInString
                }
                this.metrics.push(info)
            }
        }
    },
    data() {
        return {
            addressOracle: '',
            oracleLabel: '',
            karma: '',
            isBan: '',
            metrics: []
        }
    },
    methods: {
        handleMain() {
            router.push('/');
        }
    }
}
</script>

<style scoped>
.back {
    position: absolute;
    left: 40px;
    top: 60px;
}
.home__container {
    width: 900px;
    margin: 0 auto;   
}
.home__title {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 20px;
}
.home__cards {
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    flex-wrap: wrap;
}
</style>